import refs from "./refs";
import drawList from "./drawList";
import drawItem from "./drawItem";
import PNotify from "../node_modules/pnotify/dist/es/PNotify";

function clearOutput() {
  refs.outputList.innerHTML = "";
  refs.outputItem.innerHTML = "";
}

export default function fetchCountries(searchQuery) {
  if (searchQuery.target.value.length === 0) {
    clearOutput();
    return;
  } else {
    fetch(`https://restcountries.eu/rest/v2/name/${searchQuery.target.value}`)
      .then(response => response.json())
      .then(data => {
        if (data.length === 0) {
          clearOutput();
          console.log(data);
          return;
        } else if (data.length === 1) {
          clearOutput();
          const mkup = drawItem(data);
          refs.outputItem.insertAdjacentHTML("afterbegin", mkup);
        } else if (data.length > 10) {
          clearOutput();
          PNotify.alert({
            text: "Too many matches foound. Please, enter more specific query."
          });
        } else {
          clearOutput();
          const mkup = drawList(data);
          refs.outputList.insertAdjacentHTML("afterbegin", mkup);
        }
      })
      .catch(console.log);
  }
}
